.App {
  color: gray;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f9f9f5;
  text-align: center;
}

.error-message {
  position: absolute;
  background-color: white;
  padding: 10px;
  top: 20%;
  left: 30%;
  width: 40%;
  height: 40%;
  font-size: 12px;
  overflow: scroll;
  border: 4px solid #e5e5d5;
}

.text-card {
  background: white;
  position: absolute;
  display: table;
  top: 20%;
  left: 30%;
  width: 40%;
  height: 40%;
  font-size: 40px;
  overflow: scroll;
}

.normal {
  border: 4px solid #e5e5d5;
}

.correct {
  border: 4px solid #a2a362;
  animation: bounce 0.5s;
  animation-iteration-count: 1;
}

.incorrect {
  border: 4px solid #985952;
  animation: shake 0.1s;
  animation-iteration-count: 5;
}

@keyframes shake {
  0% { transform: translate(0px, 0px) rotate(0deg); }
  33% { transform: translate(-3px, 0px) rotate(0deg); }
  66% { transform: translate(3px, 0px) rotate(0deg);}
  100% { transform: translate(0px, 0px) rotate(0deg);}
}

@keyframes bounce {
  0% { transform: translate(0px, 0px) rotate(0deg); }
  50% { transform: translate(0px, -10px) rotate(0deg); }
  100% { transform: translate(0px, 0px) rotate(0deg);}
}

.phrase {
  display: table-cell;
  vertical-align: middle;
  top: 0; left: 0; bottom: 0; right: 0;
  text-align: center;
  margin: 50px;
  border: 1px solid #e5e5d5;
}

.welcome {
  position: absolute;
  top: 0; left: 0; bottom: 0; right: 0;
  text-align: center;
  margin: 50px;
  opacity: 0;
}

#first-sentence {
  font-size: 20px;
  animation: appear 3s;
}

#second-sentence {
  font-size: 100px;
  animation: appearandstay 4s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
}

#third-sentence {
  position: absolute;
  font-size: 30px;
  top: 120px;
  animation: appearandstay 2s;
  animation-delay: 4s;
  animation-fill-mode: forwards;
}

#third-sentence:hover {
  cursor: pointer;
  text-decoration: underline;
}

#language-selection {
  text-align: left;
  font-size: 20px;
  animation: appearandstay 2s;
  animation-fill-mode: forwards;
}

@keyframes appear {
  0% {opacity:0;}
  30% {opacity:1;}
  70% {opacity:1;}
  100% {opacity:0;}
}

@keyframes appearandstay {
  0% {opacity:0;}
  30% {opacity:1;}
  100% {opacity:1;}
}

@keyframes disappear {
  0% {opacity:1;}
  30% {opacity:0;}
  100% {opacity:0;}
}

.language-choice {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  border: 1px solid gray;
}

.language-choice:hover {
  cursor: pointer;
  background-color: #f9f9f5;
}

.language-choice:active {
  background-color: #e5e5d5;
}

.play-icon {
  position: absolute;
  bottom: 5px;
  right: 5px;
  color: #e5e5d5;
}

.play-icon:hover {
  cursor: pointer;
}

.play-icon:active {
  color: #a2a362;
}

.ban-icon {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #e5e5d5;
}

.ban-icon:hover {
  cursor: pointer;
}

.ban-icon:active {
  color: #985952;
}

.microphone-icon {
  position: absolute;
  top: 65%;
  left: 30%;
  font-size: 60px;
  color: gray;
  z-index: 1;
  padding: 10px 20px 10px 20px;
  border: 5px solid #e5e5d5;
  border-radius: 100%;
  background-color: #f9f9f5;
}

.microphone-icon:hover {
  cursor: pointer;
}

.microphone-icon:active {
  border-color: #a2a362;
}

.ellipsis {
  position: absolute;
  top: 65%;
  left: 40%;
  text-align: center;
  width: 100px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
}

.dot {
	display: inline-block;
	width: 15px;
	height: 15px;
	border-radius: 50%;
	margin-right: 10px;
  margin-top: 65%;
	background: gray;
}

.animated {
  animation: wave 1.2s linear infinite;
}

.second {
  animation-delay: .2s;
}

.third {
  animation-delay: .3s;
}

@keyframes wave {
	0%, 55%, 100% {
		transform: initial;
	}

	25% {
		transform: translateY(-20px);
	}
}

.score-bar {
  position: absolute;
  display: flex;
  align-items: center;
  text-align: center;
  top: 70%;
  left: 40%;
  font-size: 60px;
}

.outer-bar {
  position: relative;
  height: 30px;
  width: 350px;
  border-radius: 20px;
  border: 2px solid #a2a362;
  background-color: #e5e5d5;
}

.inner-bar {
  background-color: #a2a362;
  height: 100%;
  border-radius: inherit;
}

.nuntio {
  position: absolute;
  top: 15px;
  left: 15px;
  color: gray;
  font-size: 20px;
  padding: 10px;
  border: 1px solid #e5e5d5;
  border-radius: 5px;
}

.nuntio:hover {
  cursor: pointer;
  border-color: #a2a362;
}

.copyright {
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: gray;
  font-size: 10px;
}
